import { createSelector } from '../util/selectors';
import { translate } from 'internationalization/src';

var getState = function getState(globalState) {
  return globalState.food;
};

var getAnswer = createSelector([getState], function (state) {
  return state.answer;
});
var getPreviousAnswer = createSelector([getState], function (state) {
  return state.previousAnswer;
});
var getCurrent = createSelector([getState], function (state) {
  return state.current;
});
var getPrevious = createSelector([getState], function (state) {
  return state.previous;
});
var getAll = createSelector([getState], function (state) {
  return state.foods;
});
var getCurrentYes = createSelector([getCurrent, getAll], function (current, all) {
  if (current.yes && all.has(current.yes)) {
    return all.get(current.yes);
  }
});
var getCurrentNo = createSelector([getCurrent, getAll], function (current, all) {
  if (current.no && all.has(current.no)) {
    return all.get(current.no);
  }
});
var getCurrentName = createSelector([getCurrent], function (current) {
  return current.name;
});
var getCurrentQuestion = createSelector([getCurrentName], function (name) {
  return translate('theFoodYouThoughtOf', {
    name: name
  });
});
var getStage = createSelector([getState], function (state) {
  return state.stage;
});
var getHead = createSelector([getState], function (state) {
  return state.head;
});
var getFirst = createSelector([getAll, getHead], function (all, head) {
  return all.get(head);
});
var getLearningFood = createSelector([getState], function (state) {
  return state.learning;
});
export default Object.freeze({
  getAll: getAll,
  getPrevious: getPrevious,
  getPreviousAnswer: getPreviousAnswer,
  getAnswer: getAnswer,
  getCurrent: getCurrent,
  getCurrentYes: getCurrentYes,
  getCurrentNo: getCurrentNo,
  getCurrentName: getCurrentName,
  getCurrentQuestion: getCurrentQuestion,
  getStage: getStage,
  getHead: getHead,
  getFirst: getFirst,
  getLearningFood: getLearningFood
});